import globalVars from "../../../library/globalVariables";

const WebFormSelect = ({webFields}) => {

    const fieldsOptions = webFields.map(field => {
        return !field.inForm ? <option>{field.fieldName}</option> : null;
    })

    return (
        <>  
        <select name="fields">
        <option>{globalVars.blankOption}</option>
        {fieldsOptions}
        </select>
        </>
    )
}

export default WebFormSelect;